<template>
  <div class="poster-list">
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div class="content__main-operation" slot="header">
        <div class="operation-left">
          <el-button type="primary" size="small" @click="$router.push('/energy/manager/2/add')"></el-button>
          <el-button size="small" plain @click="handleDelete(0)"></el-button>
        </div>
        <div class="operation-right">
          <el-select v-model="req.cid" size="small" placeholder="" @change="reload">
            <el-option :value="0" label=""></el-option>
            <el-option
              v-for="item,idx in cate_list"
              :key="idx"
              :label="item.name"
              :value="item.id"
              :style="{'padding-left': item.style }">
            </el-option>
          </el-select>
          <el-input size="small" v-model="req.keywords" placeholder="" @keyup.enter.native="reload">
            <el-button slot="append" icon="el-icon-search" @click="reload()"></el-button>
          </el-input>
        </div>
      </div>

      <div class="content__main-list" slot="list">
        <el-table
          v-loading = "!list"
          ref="multipleTable"
          @selection-change="handleSelectionChange"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">

          <el-table-column
            type="selection"
            width="55">
          </el-table-column>

          <el-table-column
            label=""
            align="left">
            <template slot-scope="scope">
              <div class="info-item">
                <i class="item__image" :style="{backgroundImage: 'url('+scope.row.thumb+')'}"></i>
                <div class="item__info">
                  <p class="item__info-main" style="font-weight: 500;">{{scope.row.title}} <el-tag type="danger" size="mini" v-if="scope.row.isHot==1"></el-tag></p>
                  <p class="item__info-sub">{{scope.row.cname}}</p>
                  <p class="item__info-sub">{{scope.row.author}}</p>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            align="left">
            <template slot-scope="scope">
              <a :href="scope.row.videoUrl" style="color: #409EFF;text-decoration: underline;" target="_blank">{{scope.row.videoUrl}}</a>
            </template>
          </el-table-column>

          <el-table-column
            width="150"
            label=""
            align="center"
            prop="createTime">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.frozen"
                :active-text="scope.row.frozen ? '' : ''"
                @change = "changeStatus(scope.row.id, scope.row.frozen)">
              </el-switch>
            </template>
          </el-table-column>

          <el-table-column
            width="100"
            label=""
            align="center"
            prop="createTime">
          </el-table-column>

          <el-table-column
            label=""
            width="160"
            align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.row.id)"></el-button>
              <el-button
                type="danger"
                size="mini"
                @click="handleDelete(scope.row.id)"></el-button>
            </template>
          </el-table-column>

        </el-table>

        <div style="text-align: center; margin-top: 60px;" v-if="list && list.length>0">
          <el-pagination
            :page-sizes="[10, 20, 30, 40, 50]"
            :page-size="page.pageSize"
            :current-page="page.pageNo"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.totalRows"
            @size-change = "changePageSize"
            @current-change = "changePageNo">
          </el-pagination>
        </div>

      </div>
    </common-template>
  </div>

</template>

<script>
  import CommonTemplate from '@/components/common-template'
  import PosterApi from '@/api/energy.poster.js'

  export default {
    name: 'poster-list',
    components: { CommonTemplate },
    data() {
      return {
        list: null,
        req: {cid: 0, type: 2},
        page: {pageNo:1, pageSize: 20},
        choose_id_arr: [],
        cate_list: [],
      }
    },
    created() {
      this.getList()
      this.getCateList()
    },
    methods: {
      getList() {
        this.req.p = this.page.pageNo
        this.req.s = this.page.pageSize
        PosterApi.getList(this.req).then((res) => {
          this.list = res.list
          this.list.forEach(item => {
            item.frozen = Boolean(item.frozen)
          })
          this.page = res.page
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      reload() {
        this.page.pageNo = 1
        this.getList()
      },
      changePageSize(value){ // size
        this.page.pageNo = 1
        this.page.pageSize = value
        this.list = null
        this.getList()
      },
      changePageNo(value){ // currentPage
        this.page.pageNo = value
        this.list = null
        this.getList()
      },

      handleSelectionChange(val) {
        this.choose_id_arr = val
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
        this.choose_id_arr = rows
      },

      // 
      handleEdit(id) {
        this.$router.push({path:`/energy/manager/2/edit/${id}`})
      },

      // 
      changeStatus(id, flag) {
        PosterApi.batchPublish([id], !flag?0:1).then((res) => {
          this.$message.success('');
          this.reload()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      // 
      handleDelete(id) {
        let ids = [id]
        if (id == 0) { // 
          if (this.choose_id_arr.length === 0) {
            this.$message.error("");
            return
          }
          ids = this.choose_id_arr.map(item => item.id)
        }

        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          PosterApi.batchDelete(ids).then((res) => {
            this.$message.success('');
            this.reload()
          }, (err) => {
            this.$message.error(err.msg);
          })
        })
      },

      // 
      getCateList() {
        PosterApi.getCategoryChooseList(2).then((res) => {
          this.getChooseList(res.list, 0)
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      getChooseList(list, hr){
        if (!list){
          return
        }
        if (hr == 0) this.cate_list=[]
        hr += 1
        list.forEach(item => {
          var tip = 20
          for (var i=2; i<=hr; i++){
            tip += 10
          }
          this.cate_list.push({
            id: item.id,
            name: item.name,
            style: tip + 'px'
          })
          this.getChooseList(item.list, hr)
        })
      },
    }
  }
</script>

<style lang="scss">

</style>



